<template>
  <div>
    <v-container>
      <title-ribbon
        v-bind="{
          title: 'Contáctanos',
          sucursal: Number($route.params.sucursal),
        }"
      />
      <v-row class="form-contactanos__row">
        <v-col class="col-12 col-md-3 ">
          <v-row>
            <v-col cols="12 text-center text-md-left">
              <siguenos />
            </v-col>
          </v-row>
        </v-col>
        <v-col class="col-12 col-md-9 ">
          <div class="form-contactanos__box">
            <validation-observer
              ref="observer"
              v-slot="{ invalid }"
            >
              <form @submit.prevent="validateForm($route.params.sucursal)">
                <v-card class="mx-0 my-0">
                  <v-row
                    align="start"
                    justify="center"
                  >
                    <v-col class="col-12 col-lg-6">
                      <v-row>
                        <v-col>
                          <validation-provider
                            v-slot="{ errors }"
                            rules="required|max:100"
                            name="Nombre"
                          >
                            <label class="grey--text">
                              Nombre {{ api }}
                            </label>
                            <v-text-field
                              v-model="nombre"
                              :error-messages="errors"
                              color="success"
                              filled
                              dense
                            />
                          </validation-provider>
                          <validation-provider
                            v-slot="{ errors }"
                            rules="required|email|max:100"
                            name="Email"
                          >
                            <label class="grey--text">
                              E-mail
                            </label>
                            <v-text-field
                              v-model="email"
                              :error-messages="errors"
                              color="success"
                              filled
                              dense
                            />
                          </validation-provider>
                          <validation-provider
                            v-slot="{ errors }"
                            rules="required|numeric|max:50"
                            name="Telefono"
                          >
                            <label class="grey--text">
                              Teléfono
                            </label>
                            <v-text-field
                              v-model="telefono"
                              :error-messages="errors"
                              color="success"
                              type="number"
                              filled
                              dense
                            />
                          </validation-provider>
                        </v-col>
                      </v-row>
                    </v-col>

                    <v-col class="col-12 col-lg-6">
                      <v-row>
                        <v-col>
                          <validation-provider
                            v-slot="{ errors }"
                            rules="required|max:100"
                            name="Asunto"
                          >
                            <label class="grey--text">
                              Asunto
                            </label>
                            <v-select
                              v-model="asunto"
                              :error-messages="errors"
                              color="success"
                              :items="asuntos"
                              item-text="Asunto"
                              :menu-props="{ maxHeight: '400' }"
                              persistent-hint
                              filled
                              dense
                            />
                          </validation-provider>
                          <validation-provider
                            v-slot="{ errors }"
                            rules="required|max:700"
                            name="Mensaje"
                          >
                            <label class="grey--text">
                              Mensaje
                            </label>
                            <v-text-field
                              v-model="mensaje"
                              :error-messages="errors"
                              color="success"
                              filled
                              dense
                              height="170"
                              class="mensaje"
                            />
                          </validation-provider>
                          <div style="height: 45px;">
                            <fade-transition>
                              <v-alert
                                v-show="showRecaptchaAlert"
                                dense
                                text
                                type="warning"
                                style="padding: 1px; margin: 0px;"
                                class="box"
                              >
                                Marque la casilla de verificación
                              </v-alert>
                            </fade-transition>
                            <fade-transition>
                              <v-alert
                                v-show="showEnviado"
                                dense
                                text
                                type="success"
                                color="success"
                                style="padding: 1px; margin: 0px;"
                                class="box"
                              >
                                Formulario enviado
                              </v-alert>
                            </fade-transition>
                          </div>
                          <!-- testing -->
                          <!-- <vue-recaptcha
                            sitekey="6LeIxAcTAAAAAJcZVRqyHh71UMIEGNQ_MXjiZKhI"
                            @verify="onVerify"
                            @expired="onExpired"
                          /> -->
                          <!-- production -->
                          <vue-recaptcha
                            sitekey="6Lf7HQQeAAAAAC7MOA15SB35MQ8UwUh8wYG5rg-h"
                            @verify="onVerify"
                            @expired="onExpired"
                          />
                          <!-- mobile -->
                          <!-- <vue-recaptcha
                          sitekey="6Le-Bu8bAAAAAOIi7Am_cyMl4OqY0rn8fg7UI9ry"
                          @verify="onVerify"
                          @expired="onExpired"
                        /> -->
                          <div class="form-contactanos__buttons">
                            <v-btn
                              color="success"
                              default
                              type="submit"
                              :disabled="invalid"
                              :loading="btnLoader"
                            >
                              Enviar
                            </v-btn>
                          </div>
                        </v-col>
                      </v-row>
                    </v-col>
                  </v-row>
                </v-card>
              </form>
            </validation-observer>
          </div>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>
<script>
  import Vue from 'vue'
  import VueRecaptcha from 'vue-recaptcha'
  import { FadeTransition } from 'vue2-transitions'
  export default {
    name: 'FormularioContactanos',
    components: {
      TitleRibbon: () => import('@/components/web/base/TitleRibbon'),
      Siguenos: () => import('@/components/web/base/Siguenos'),
      VueRecaptcha,
      FadeTransition,
    },
    data: () => ({
      nombre: '',
      asunto: '',
      email: '',
      telefono: '',
      mensaje: '',
      asuntos: ['Agradecimiento', 'Consulta', 'Reclamo', 'Sugerencia', 'Otro'],
      recaptchaVerified: false,
      showRecaptchaAlert: false,
      showEnviado: false,
      btnLoader: false,
    }),
    watch: {
      recaptchaVerified: function () {
        if (this.recaptchaVerified) {
          this.hideMsg()
        }
      },
    },
    methods: {
      async validateForm (sucursal) {
        const isValid = await this.$refs.observer.validate()
        if (!isValid) {
          return false
        }

        if (!this.recaptchaVerified) {
          this.showRecaptchaAlert = true
          return false
        }

        this.btnLoader = true

        const body = {
          nombre: this.nombre,
          email: this.email,
          telefono: this.telefono,
          asunto: this.asunto,
          mensaje: this.mensaje,
          sucursal: Number(sucursal),
        }

        Vue.axios
          .post('/api/formularios/contactanos', body)
          .then(response => {
            this.showEnviadoMsg()
            this.limpiarFormulario()
            this.btnLoader = false
          })
          .catch(e => {
            console.log(e)
          })
      },
      onVerify () {
        if (!this.recaptchaVerified) {
          this.recaptchaVerified = true
        }
      },
      onExpired () {
        this.recaptchaVerified = false
      },
      hideMsg () {
        this.showRecaptchaAlert = false
      },
      showEnviadoMsg () {
        this.showEnviado = true
        setTimeout(() => {
          this.showEnviado = false
        }, 10000)
      },
      limpiarFormulario () {
        this.nombre = ''
        this.asunto = ''
        this.email = ''
        this.telefono = ''
        this.mensaje = ''
        requestAnimationFrame(() => {
          this.$refs.observer.reset()
        })
      },
    },
    metaInfo: {
      title: 'Contactanos',
      titleTemplate: '%s - Centro de Terapia del Comportamiento',
      htmlAttrs: {
        lang: 'es',
        amp: true,
      },
    },
  }
</script>
